import React, { useRef } from 'react';
import Camera from "./lib/camera"

function capture(imgSrc) {
  console.log(imgSrc);
}

function App() {
  const cam = useRef(null);
  return (
    <div className="App">
      <Camera
        showFocus={true}
        front={false}
        capture={capture}
        ref={cam}
        width="80%%"
        height="auto"
        focusWidth="80%"
        focusHeight="60%"
        btnColor="white"
      />
      <button onClick={img => cam.current.capture(img)}>Take image</button>
    </div>
  );
}

export default App;
